@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);

*{
    scroll-behavior: smooth;
}

.app{
    min-height: 100vh;
    background-color: #39445a;
    color: #fff;
    padding-top: 70px;
    padding-bottom: 70px;
    font-family: 'Montserrat',sans-serif;

}

.pageTitle{
    text-transform: uppercase;
    display: flex;
    justify-content: end;
    font-family: 'Montserrat',sans-serif;
    font-size: 2vw;
    padding: 20px;
    border-radius: 50px;
    color: #fff;
}

@media (max-width: 700px) {
    .app {
        padding-top: 70px;
    }
    .pageTitle{
        font-size: 3vh;
    }
}


.header {
    padding: 10px 50px;
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: start;
    text-transform: uppercase;
    background-color: #39445a;
    font-family: "Montserrat", sans-serif;
    font-size: 5vh;
    box-shadow: 0 1px 5px black;
    color: #fff;
    z-index: 100;
    font-weight: 800;
  }
.trending {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
}
.media {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.media:hover {
    background-color: #fff;
    color: #333;
    cursor: pointer;
}

@media (max-width: 550px) {
    .media {
        width: 46%;
    }
}

.poster {
    border-radius: 10px;
}

.title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}

.subTitle {
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
}
.content_landscape {
  padding: 9px;
  border-radius: 15px;
}

.content_portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
  
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.content_about {
  padding: 10px;
  width: 100%;
  height: 70%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.content_title {
  height: 12%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content_description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin;
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
}

.content_description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .content_landscape {
    display: none;
  }
  .content_portrait {
    display: flex;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-evenly;
    padding: 10px 0 10px 10px;
  }
  .content_about {
    width: 80%;
    padding: 0px 10px;
    height: 100%;
  }
  .content_title {
    font-size: 3.5vw;
  }
  .content_description {
    font-size: 22px;
  }
}

.input_prop {
  color: #fff !important;
}

